<template>
  <div id="app">
    <el-container>
      <el-header height="400">
        <h1 style="text-align:center">minium 测试报告</h1>
      </el-header>
      <el-container>
        <el-aside width="500px">
          <el-col>
            <el-row>
              <!-- 显示报告摘要 -->
              <el-card>
                <p>用例总数: {{summary.case_num}}</p>
                <p>失败用例: {{summary.failed}}</p>
                <p>错误用例: {{summary.error}}</p>
                <p>平均耗时: {{summary.costs}}</p>
              </el-card>
            </el-row>
            <el-row style="padding-top:2em">
              <el-table
                ref="singleTable"
                :data="caseDatas"
                style="width: 100%"
                highlight-current-row
                v-on:row-click="tableRowClick"
              >
                <el-table-column prop="run_time" label="执行时间" width="180"></el-table-column>
                <el-table-column prop="case_name" label="用例名称" width="180"></el-table-column>
                <el-table-column prop="case_name" label="状态" width="100">
                  <template slot-scope="scope">
                    <el-tag :type="statusClass(scope.row)" effect="”dark">{{ statusMsg(scope.row) }}</el-tag>
                  </template>
                </el-table-column>
              </el-table>
            </el-row>
          </el-col>
        </el-aside>
        <el-main>
          <CaseDetail v-if="caseDetail" :caseDetail="caseDetail"/>
        </el-main>
      </el-container>
      <el-footer style="text-align:center">
        <p>Minium Designed by 微信测试中心</p>
        <p>Copyright &copy; 1998-2019 Tencent Inc. All Rights Reserved</p>
      </el-footer>
    </el-container>
    <el-backtop>
      <div
      style="{
        height: 100%;
        width: 100%;
        background-color: #f2f5f6;
        box-shadow: 0 0 6px rgba(0,0,0, .12);
        text-align: center;
        line-height: 40px;
        color: #1989fa;
      }"
    >
      Top
    </div>
    </el-backtop>
  </div>
</template>

<script>
import CaseDetail from './components/CaseDetail'
const axios = require('axios')

// let metadata = {}
// if(process.env.NODE_ENV === 'production'){
//   metadata = "pleacereplaceme"
// }else{
//   metadata = require('./meta.json')
// }

export default {
  name: 'app',
  components: { CaseDetail },
  data () {
    return {
      metadata: null,
      caseDetailIndex: 1,
      caseDetail: 0
    }
  },
  mounted () {
    axios.get(`${process.env.BASE_URL}meta.json`).then(resp => {
      this.metadata = resp.data
      this.$refs.singleTable.setCurrentRow(this.caseDetail = this.metadata.case_datas[0])
    })
  },
  methods: {
    tableRowClassName ({ row, index }) {
      if (row.success) {
        return 'success-row'
      } else if (row.failure) {
        return 'warning-row'
      }
      return 'error-row'
    },
    tableRowClick (detail) {
      console.log(detail)
      this.caseDetail = detail
    },
    statusClass (caseDetail) {
      if (caseDetail.success) {
        return 'success'
      }
      if (caseDetail.is_error) {
        return 'danger'
      }
      if (caseDetail.is_failure) {
        return 'warning'
      }
    },
    statusMsg (caseDetail) {
      if (caseDetail.success) {
        return '成功'
      }
      if (caseDetail.is_error) {
        return '异常'
      }
      if (caseDetail.is_failure) {
        return '失败'
      }
    }
  },
  computed: {
    caseDatas () {
      if (this.metadata) {
        return this.metadata.case_datas
      } else {
        return []
      }
    },
    summary () {
      if (this.metadata) {
        return this.metadata.summary
      }
      return {}
    }
  }
}
</script>

<style>
.el-table .warning-row {
  background: #e6a23c;
}

.el-table .success-row {
  background: #67c23a;
}

.el-table .error-row {
  background: #f56c6c;
}
</style>
