  padding: 1em 0 0 0.5em;
  background-color: #3c3f41;
}
.log-debug {
  color: #c1c6cf;
}
.log-info {
  color: #67c23a;
}
.log-warning {
  color: #e6a23c;
}
.log-error {
  color: #f56c6c;
}
</style>
